  text-decoration: underline;
}

.container {
  max-width: 1210px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  width: 100%;
}

.container:before {
  content: '';
  display: table;
}

.container:after {
  content: '';
  display: table;
  clear: both;
}

body {
  color: #222222;
  font-family: "Lato", sans-serif;
  font-size: 14px;
  overflow-x: hidden;
}

#xh-pannel {
  background: #fff;
}

