  transform: translateY(-50%);
  text-align: center;
  left: 0;
  right: 0;
}

.inline-block {
  display: inline-block;
}

.clear {
  clear: both;
  float: none;
}

.clearfix::after {
  clear: both;
  content: '';
  display: table;
}

.hidden {
  display: none !important;
}

.hide-text {
  overflow: hidden;
  padding: 0;
  text-indent: 101%;
  white-space: nowrap;
}

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

